export default {
	name: "praise",
	data() {
		return {
			animationData: {},
		}
	},
	created() {
		// 组件创建时,创建一个临时动画对象

		this.animation = uni.createAnimation();

	},
	beforeDestroy() {
		// 组件销毁时,清楚动画数据
		this.animationData = {};
	},
	methods: {

		praiseMe() {
			this.animation.translateY(-60).opacity(1).step({
				duration: 400
			});
			// 导出动画数据到组件
			this.animationData = this.animation.export();
			// 还原动画
			setTimeout(function() {
				this.animation.translateY(0).opacity(0).step({
					duration: 0
				});
				this.animationData = this.animation.export();
			}.bind(this), 500);
		},




	}
}
